import oneArticle from './oneArticle.vue'

var loadArticleFlag = false; // 为加载文章加锁
export default {
		
	components:{
		oneArticle,
	},
	data() {
		return {
			userInfo:'',
			myArticleList: [],
			totalPage: 1,
			currentPage: 1
		}
	},
	
	onLoad() {
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)) {
			uni.redirectTo({
				url: "../signin/signin"
			})
			return;
		}else{
			this.userInfo = userInfo; // 刷去默认值(若有)
		}
		
		this.mySocket.init(); // 初始化 Socket, 离线调试请注释掉
		var page = this.currentPage;
		this.showArticles(page);
	},
	methods: {
		// 锁
		showArticles: function(page){
			if(loadArticleFlag){
				return;
			}
			loadArticleFlag = true;
			uni.showLoading({
				title:"加载中..."
			})
			setTimeout(()=>{
				if(loadArticleFlag){
					loadArticleFlag = false; //解锁
					uni.hideLoading();
					uni.showToast({
						title: "网络未知错误",
						icon: "none",
						duration: 1000
					})
				}
			}, 5000); //延时五秒timeout
			
			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/queryPublishHistory',
				method: "POST",
				data: {
					page: page,
					userId: that.userInfo.id,
					targetId: that.userInfo.id //应该为targetId,但缺少publicProfile的数据传输,暂时用userId测试
				},
				header:{
					'content-type': 'application/x-www-form-urlencoded'
				},
				success:(res) => {
					setTimeout(() => { //延时加载
						uni.hideLoading();
						loadArticleFlag = false;
						
						console.log(res);
						if(page == 1){
							that.myArticleList = [];
						}
						var newArticleList = res.data.data.rows;
						var oldArticleList = that.myArticleList;
						that.myArticleList = oldArticleList.concat(newArticleList);
						that.currentPage = page;
						that.totalPage = res.data.data.total;
					}, 300);
				},
				fail: (res) => {
					uni.hideLoading();
					loadArticleFlag = false;
					
					console.log("index unirequest fail");
					console.log(res);
				}
			});	
		},
		loadMore: function() {
			console.log("11");
			var that = this;
			var currentPage = that.currentPage;
			console.log(currentPage);
			var totalPage = that.totalPage;
			console.log(totalPage);
			// 判断当前页数和总页数是否相等
			if (currentPage == totalPage) {
				// that.showArticles(1);
				uni.showToast({
					title: "没有更多文章了",
					icon: "none",
					duration: 1000
				})
			} else {
				var page = currentPage + 1;
				that.showArticles(page);
			}
		}
	}
}
